'use client';

import { useEffect, useMemo, useState } from 'react';
import { useRouter } from 'next/navigation';
import { useSession } from 'next-auth/react';
import { Input } from '@/components/ui/input';
import { Button } from '@/components/ui/button';
import { Label } from '@/components/ui/label';

const publishOptions = ['draft', 'published'] as const;
type PublishStatus = (typeof publishOptions)[number];

interface BeneficiaryMessageInput {
  beneficiaryId: string;
  title: string;
  beneficiaryMessageText: string; // always string in the textarea
  status: PublishStatus;
}

interface InitialData {
  id: string;
  beneficiaryId: string;
  title: string;
  beneficiaryMessageText?: unknown; // JSON from API
  status: PublishStatus;
}

const s = (v: unknown) => (typeof v === 'string' ? v : '');
const normalizeId = (v?: string) => (v && v !== 'undefined' && v !== 'null' ? v : '');

export default function CreateBeneficiaryMessageForm({
  beneficiaryId,
  editId,
  initialData,
  onSuccess,
  onCancel,
}: {
  beneficiaryId?: string; // now optional; server resolves for beneficiaries
  editId?: string;
  initialData?: InitialData;
  onSuccess?: () => void;
  onCancel?: () => void;
}) {
  const router = useRouter();
  const { data: session } = useSession();
  const role = (session?.user?.role ?? 'guest') as
    | 'super'
    | 'admin'
    | 'moderator'
    | 'beneficiary'
    | 'guest';
  const isBeneficiary = role === 'beneficiary';

  // robust edit detection
  const effectiveEditId = useMemo(
    () => normalizeId(editId) || normalizeId(initialData?.id),
    [editId, initialData?.id]
  );
  const isEditing = Boolean(effectiveEditId);

  const [form, setForm] = useState<BeneficiaryMessageInput>({
    beneficiaryId: s(beneficiaryId),
    title: '',
    beneficiaryMessageText: '',
    status: 'draft',
  });

  const [loading, setLoading] = useState(false);
  const [loadingInitial, setLoadingInitial] = useState(isEditing && !initialData);

  // Stringify any JSON for the input value
  const toText = (msg: unknown) => {
    if (typeof msg === 'string') return msg;
    try {
      return JSON.stringify(msg ?? '', null, 2);
    } catch {
      return '';
    }
  };

  // Load for edit or set create defaults
  useEffect(() => {
    if (initialData) {
      setForm({
        beneficiaryId: s(initialData.beneficiaryId) || s(beneficiaryId),
        title: s(initialData.title),
        beneficiaryMessageText: toText(initialData.beneficiaryMessageText),
        status: initialData.status ?? 'draft',
      });
      setLoadingInitial(false);
      return;
    }

    if (isEditing && effectiveEditId) {
      setLoadingInitial(true);
      fetch(`/api/beneficiaryMessages/${effectiveEditId}`)
        .then((res) => {
          if (!res.ok) throw new Error('Failed to fetch beneficiary message');
          return res.json();
        })
        .then((data) => {
          setForm({
            beneficiaryId: s(data.beneficiaryId) || s(beneficiaryId),
            title: s(data.title),
            beneficiaryMessageText: toText(data.beneficiaryMessageText),
            status: (publishOptions as readonly string[]).includes(data.status)
              ? (data.status as PublishStatus)
              : 'draft',
          });
        })
        .catch(() => {
          alert('Error loading existing message data.');
        })
        .finally(() => setLoadingInitial(false));
      return;
    }

    // Create defaults
    setForm({
      beneficiaryId: s(beneficiaryId),
      title: '',
      beneficiaryMessageText: '',
      status: 'draft',
    });
  }, [initialData, isEditing, effectiveEditId, beneficiaryId]);

  // If user is a beneficiary, always clamp status to draft
  useEffect(() => {
    if (isBeneficiary && form.status !== 'draft') {
      setForm((prev) => ({ ...prev, status: 'draft' }));
    }
  }, [isBeneficiary, form.status]);

  const handleChange = (e: React.ChangeEvent<HTMLInputElement | HTMLSelectElement>) => {
    const { name, value } = e.target;
    setForm((prev) => ({
      ...prev,
      [name]: name === 'status' ? (value as PublishStatus) || 'draft' : s(value),
    }));
  };

  const handleMessageChange = (e: React.ChangeEvent<HTMLTextAreaElement>) => {
    setForm((prev) => ({ ...prev, beneficiaryMessageText: s(e.target.value) }));
  };

  const handleSubmit = async (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    setLoading(true);

    try {
      let messageToSend: unknown = form.beneficiaryMessageText;
      try {
        messageToSend = JSON.parse(form.beneficiaryMessageText);
      } catch {
        // keep as string if not valid JSON
      }

      const url = isEditing
        ? `/api/beneficiaryMessages/${effectiveEditId}`
        : '/api/beneficiaryMessages';
      const method = isEditing ? 'PATCH' : 'POST';

      // Beneficiary: server resolves their beneficiaryId via session names; force draft
      // Admin/Mod/Super: must provide beneficiaryId on create; can choose status
      let payload: Record<string, unknown>;
      if (isEditing) {
        payload = {
          title: form.title,
          beneficiaryMessageText: messageToSend,
          status: isBeneficiary ? 'draft' : form.status,
        };
      } else if (isBeneficiary) {
        payload = {
          title: form.title,
          beneficiaryMessageText: messageToSend,
          status: 'draft',
        };
      } else {
        if (!form.beneficiaryId) {
          alert('Please select a beneficiary before creating a message.');
          setLoading(false);
          return;
        }
        payload = {
          beneficiaryId: form.beneficiaryId,
          title: form.title,
          beneficiaryMessageText: messageToSend,
          status: form.status,
        };
      }

      const res = await fetch(url, {
        method,
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(payload),
      });

      if (!res.ok) {
        const error = await res.json().catch(() => ({}));
        throw new Error(error.error || 'Failed to save beneficiary message');
      }

      onSuccess ? onSuccess() : router.refresh();
    } catch {
      alert('There was an error saving the beneficiary message.');
    } finally {
      setLoading(false);
    }
  };

  if (loadingInitial) {
    return (
      <div className="flex items-center justify-center min-h-40">
        <span className="text-gray-600">Loading...</span>
      </div>
    );
  }

  return (
    <form
      onSubmit={handleSubmit}
      className="max-w-xl mx-auto space-y-6 p-6 bg-background rounded-xl shadow"
      autoComplete="off"
    >
      <h2 className="font-semibold text-xl mb-4">
        {isEditing ? 'Edit Beneficiary Message' : 'Create Beneficiary Message'}
      </h2>

      {/* Hidden parent id: used on CREATE for admins/mods; ignored by server for beneficiaries */}
      <Input type="hidden" name="beneficiaryId" value={form.beneficiaryId} />

      <div className="space-y-2">
        <Label htmlFor="title">Title</Label>
        <Input id="title" name="title" value={form.title} onChange={handleChange} required />
      </div>

      <div className="space-y-2">
        <Label htmlFor="beneficiaryMessageText">Message (JSON or text)</Label>
        <textarea
          id="beneficiaryMessageText"
          name="beneficiaryMessageText"
          className="w-full min-h-[140px] border border-input rounded-md p-2 text-sm bg-background text-foreground"
          value={form.beneficiaryMessageText}
          onChange={handleMessageChange}
          required
        />
      </div>

      {/* Status: beneficiaries cannot publish; hide the selector and show a note */}
      {isBeneficiary ? (
        <div className="space-y-1">
          <Label>Publish Status</Label>
          <div className="text-sm text-gray-600">
            Your message will be saved as <span className="font-semibold">draft</span>. An admin can
            publish it later.
          </div>
        </div>
      ) : (
        <div className="space-y-2">
          <Label htmlFor="status">Publish Status</Label>
          <select
            id="status"
            name="status"
            value={form.status ?? 'draft'}
            onChange={handleChange}
            className="w-full border border-input rounded-md p-2 text-sm bg-background text-foreground"
          >
            {publishOptions.map((status) => (
              <option key={status} value={status}>
                {status}
              </option>
            ))}
          </select>
        </div>
      )}

      <div className="flex gap-3">
        <Button
          type="submit"
          disabled={loading || (isEditing && !effectiveEditId)}
          className="flex-1 bg-[#9f004d] hover:bg-[#7c003c] text-white"
        >
          {loading
            ? isEditing
              ? 'Saving...'
              : 'Creating...'
            : isEditing
            ? 'Save Changes'
            : 'Create Message'}
        </Button>
        {onCancel && (
          <Button
            type="button"
            variant="outline"
            className="flex-1 border-[#9f004d] text-[#9f004d] hover:bg-[#f5e3ec]"
            onClick={onCancel}
            disabled={loading}
          >
            Cancel
          </Button>
        )}
      </div>
    </form>
  );
}
