'use client';

import { useSession } from 'next-auth/react';
import { useRouter, useSearchParams, usePathname } from 'next/navigation';
import { useEffect, useState, useMemo, useRef } from 'react';
import * as XLSX from 'xlsx';
import DownloadColumnsModal, { ColumnOption } from './components/downloadColumnsModal';
import { handleDownloadPDF } from './components/handleDownloadPDF';
import { permissions } from '@/lib/permissions';

// Icons
import {
  FaUserCog,
  FaCalendarAlt,
  FaFilePdf,
  FaUniversity,
  FaUsers,
  FaEnvelope,
  FaClipboardList,
  FaReply,
  FaChartPie,
  FaHome,
  FaAngleLeft,
  FaAngleRight,
} from 'react-icons/fa';

// Section components
import ProjectsSection from './components/sections/ProjectsSection';
import ReportsSection from './components/sections/ReportsSection';
import EventsSection from './components/sections/EventsSection';
import InstitutionsSection from './components/sections/InstitutionsSection';
import BeneficiariesSection from './components/sections/BeneficiariesSection';
import BeneficiaryMessagesSection from './components/sections/BeneficiaryMessagesSection';
import BeneficiaryRequestsSection from './components/sections/BeneficiaryRequestsSection';
import BeneficiaryRequestResponseSection from './components/sections/BeneficiaryRequestResponseSection';
import ChartsSection from './components/sections/ChartsSection';
import AdminSection from './components/sections/AdminSection';
import HomePageSection from './components/sections/HomePageSection';

// Import all edit/create forms
import CreateProjectForm from './createProjectForm';
import CreateReportForm from './createReportForm';
import CreateEventForm from './createEventForm';
import CreateInstitutionForm from './createInstitutionForm';
import CreateBeneficiaryForm from './createBeneficiaryForm';
import CreateBeneficiaryMessageForm from './createBeneficiaryMessageForm';
import CreateBeneficiaryRequestForm from './createBeneficiaryRequestForm';
import CreateBeneficiaryRequestResponseForm from './createBeneficiaryRequestResponseForm';
import CreateAdminForm from './createUserForm';
import CreateHomePageForm from './createHomePageForm';

// Import all view-only components (add these for each section that needs "View" support)
import BeneficiaryView from './components/views/beneficiaryView';
import BeneficiaryMessageView from './components/views/beneficiaryMessageView';
import BeneficiaryRequestView from './components/views/beneficiaryRequestView';
import BeneficiaryRequestResponseView from './components/views/beneficiaryRequestResponseView';
import EventView from './components/views/eventView';
import FAQView from './components/views/FAQView';
import HomePageView from './components/views/homePageView';
import InstitutionView from './components/views/institutionView';
import MessageView from './components/views/messageView';
import ProjectView from './components/views/projectView';
import ReportView from './components/views/reportView';
import UserView from './components/views/userView';
// import other View components as needed

// Table controls component for search/filter/add/export
function TableControls({
  search,
  setSearch,
  onAddNew,
  exportExcel,
  downloadPDF,
  activeSection,
  sectionLabels,
}: {
  search: string;
  setSearch: (s: string) => void;
  onAddNew: () => void;
  exportExcel: () => void;
  downloadPDF: () => void;
  activeSection: string;
  sectionLabels: Record<string, string>;
}) {
  return (
    <div className="flex flex-wrap items-center justify-between mb-4 gap-2">
      <div className="flex items-center gap-2 flex-1">
        <input
          type="text"
          className="border px-3 py-2 rounded w-64 focus:outline-pink-500 dark:bg-gray-800 dark:text-white"
          placeholder={`Search ${sectionLabels[activeSection] || 'records'}...`}
          value={search}
          onChange={(e) => setSearch(e.target.value)}
        />
        <button
          className="px-2 py-1 border rounded text-sm bg-green-50 hover:bg-green-100 dark:bg-gray-800 dark:hover:bg-green-900"
          title="Export to Excel"
          onClick={exportExcel}
        >
          Export to Excel
        </button>
        <button
          className="px-2 py-1 border rounded text-sm bg-yellow-50 hover:bg-yellow-100 dark:bg-gray-800 dark:hover:bg-yellow-900"
          title="Download as PDF"
          onClick={downloadPDF}
        >
          Download as PDF
        </button>
      </div>
      <div>
        <button
          className="px-4 py-2 bg-pink-700 text-white rounded hover:bg-pink-900 text-sm font-semibold"
          onClick={onAddNew}
        >
          Add New {sectionLabels[activeSection]}
        </button>
      </div>
    </div>
  );
}

const sections: Section[] = [
  'charts',
  'projects',
  'events',
  'reports',
  'institutions',
  'beneficiaries',
  'beneficiaryMessages',
  'beneficiaryRequests',
  'beneficiaryRequestResponse',
  'Home Page',
  'admin',
] as const;

type Section = (typeof sections)[number];

const sectionFeatures: Record<Section, { apiRoute?: string }> = {
  projects: { apiRoute: '/api/projects' },
  reports: { apiRoute: '/api/reports' },
  events: { apiRoute: '/api/events' },
  institutions: { apiRoute: '/api/institutions' },
  beneficiaries: { apiRoute: '/api/beneficiaries' },
  beneficiaryMessages: { apiRoute: '/api/beneficiaryMessages' },
  beneficiaryRequests: { apiRoute: '/api/beneficiaryRequests' },
  beneficiaryRequestResponse: { apiRoute: '/api/beneficiaryRequestResponse' },
  charts: {},
  'Home Page': { apiRoute: '/api/homepage-content' },
  admin: { apiRoute: '/api/users' },
};

const sectionIcons: Record<Section, React.ReactNode> = {
  charts: <FaChartPie className="text-pink-600 dark:text-pink-400" />,
  projects: <FaClipboardList className="text-yellow-600 dark:text-yellow-400" />,
  events: <FaCalendarAlt className="text-green-600 dark:text-green-400" />,
  reports: <FaFilePdf className="text-red-600 dark:text-red-400" />,
  institutions: <FaUniversity className="text-blue-600 dark:text-blue-400" />,
  beneficiaries: <FaUsers className="text-indigo-600 dark:text-indigo-400" />,
  beneficiaryMessages: <FaEnvelope className="text-orange-500 dark:text-orange-400" />,
  beneficiaryRequests: <FaClipboardList className="text-teal-600 dark:text-teal-400" />,
  beneficiaryRequestResponse: <FaReply className="text-purple-600 dark:text-purple-400" />,
  'Home Page': <FaHome className="text-pink-700 dark:text-pink-400" />,
  admin: <FaUserCog className="text-gray-700 dark:text-gray-300" />,
};

const sectionLabels: Record<Section, string> = {
  charts: 'Charts',
  projects: 'Projects',
  events: 'Events',
  reports: 'Reports',
  institutions: 'Institutions',
  beneficiaries: 'Beneficiaries',
  beneficiaryMessages: 'Beneficiary Messages',
  beneficiaryRequests: 'Beneficiary Requests',
  beneficiaryRequestResponse: 'Beneficiary Request Response',
  'Home Page': 'Home Page',
  admin: 'Admin',
};

export default function AdminDashboardPage() {
  const { data: session, status } = useSession();
  const router = useRouter();
  const [activeSection, setActiveSection] = useState<Section>('charts');
  const [sidebarCollapsed, setSidebarCollapsed] = useState(false);
  const [data, setData] = useState<any[]>([]);
  const [editRecord, setEditRecord] = useState<any | null>(null);
  const [viewRecord, setViewRecord] = useState<any | null>(null);
  const [deleteId, setDeleteId] = useState<string | number | null>(null);
  const [deleteLoading, setDeleteLoading] = useState(false);
  const [sortBy, setSortBy] = useState('createdAt');
  const [sortOrder, setSortOrder] = useState<'asc' | 'desc'>('desc');
  const [rowsPerPage, setRowsPerPage] = useState<number>(10);
  const [page, setPage] = useState<number>(1);
  const [search, setSearch] = useState('');
  const sectionRef = useRef(activeSection);

  const searchParams = useSearchParams();
  const pathname = usePathname();

  const [downloadModalOpen, setDownloadModalOpen] = useState(false);
  const [downloadColumns, setDownloadColumns] = useState<ColumnOption[]>([]);
  const [pendingDownloadData, setPendingDownloadData] = useState<any[]>([]);

  useEffect(() => {
    const searchType = searchParams.get('type') as Section | null;
    const defalultSection: Section = 'charts';
    const sectionToLoad =
      searchType && sections.includes(searchType) ? searchType : defalultSection;
    handleMenuClick(sectionToLoad);
    // eslint-disable-next-line
  }, []);

  async function handleMenuClick(section: Section) {
    const newUrl = `${pathname}?type=${section}`;
    router.replace(newUrl);
    setActiveSection(section);
    setEditRecord(null);
    setViewRecord(null);
    setDeleteId(null);
    setPage(1);
    sectionRef.current = section;

    const feat = sectionFeatures[section];
    if (feat?.apiRoute) {
      try {
        const res = await fetch(feat.apiRoute);
        if (!res.ok) {
          setData([]);
          return;
        }
        const rawData = await res.json();
        setData(rawData);
      } catch {
        setData([]);
      }
    } else {
      setData([]);
    }
  }

  const sortedData = useMemo(() => {
    if (!data) return [];
    if (!activeSection || !sectionFeatures[activeSection]?.apiRoute) return data;
    let sorted = [...data];
    sorted.sort((a, b) => {
      let valA = a[sortBy];
      let valB = b[sortBy];
      if (sortBy === 'createdAt' || sortBy === 'updatedAt') {
        valA = new Date(valA).getTime();
        valB = new Date(valB).getTime();
      }
      if (valA < valB) return sortOrder === 'asc' ? -1 : 1;
      if (valA > valB) return sortOrder === 'asc' ? 1 : -1;
      return 0;
    });
    return sorted;
  }, [data, sortBy, sortOrder, activeSection]);

  const filteredData = useMemo(() => {
    if (!sortedData) return [];
    if (!activeSection || !search.trim()) return sortedData;
    const lower = search.toLowerCase();
    return sortedData.filter((item) =>
      Object.keys(item).some(
        (key) => typeof item[key] === 'string' && item[key].toLowerCase().includes(lower)
      )
    );
  }, [sortedData, search, activeSection]);

  const pageCount = Math.ceil((filteredData?.length ?? 0) / rowsPerPage);
  const paginatedData = useMemo(
    () => (filteredData ?? []).slice((page - 1) * rowsPerPage, page * rowsPerPage),
    [filteredData, page, rowsPerPage]
  );

  // Universal edit handler for all sections
  function handleEdit(record: any) {
    setEditRecord(record);
  }
  function handleCancelEdit() {
    setEditRecord(null);
  }
  async function handleSaveEdit() {
    setEditRecord(null);
    await handleMenuClick(activeSection);
  }

  // Universal view handler for all sections
  function handleView(record: any) {
    setViewRecord(record);
  }
  function handleCloseView() {
    setViewRecord(null);
  }

  // Universal delete handler for all sections
  async function handleDelete(id: string | number) {
    setDeleteId(id);
    setDeleteLoading(true);
    try {
      let apiRoute = sectionFeatures[activeSection]?.apiRoute;
      if (!apiRoute) throw new Error('Delete API route not defined');
      if (apiRoute.endsWith('/')) apiRoute = apiRoute.slice(0, -1);
      const url = `${apiRoute}/${id}`;
      console.log('Attempting DELETE:', url);

      const res = await fetch(url, { method: 'DELETE' });

      const text = await res.text();
      console.log('DELETE response:', res.status, text);

      if (!res.ok) {
        alert(`Failed to delete record: ${res.status}\n${text}`);
      } else {
        await handleMenuClick(activeSection); // reload data
      }
    } catch (err) {
      alert('Error deleting record: ' + err);
    } finally {
      setDeleteLoading(false);
      setDeleteId(null);
    }
  }

  // TableActions logic for export/download
  function getCurrentColumns(): any[] {
    // You should implement this to return the correct columns for the current section
    // For example, if you have a columns function, call it here
    // return beneficiaryMessageColumns({...});
    return []; // <-- Replace with actual columns
  }
  const handleExportExcel = () => {
    const columns = getCurrentColumns();
    const headers = columns
      .filter((col) => col.header && col.header !== 'Actions' && col.header !== 'Delete')
      .map((col) => (typeof col.header === 'function' ? col.header({}) : col.header));
    const exportableCols = columns.filter(
      (col) => col.header && col.header !== 'Actions' && col.header !== 'Delete'
    );
    const rows = paginatedData.map((row, idx) =>
      exportableCols.map((col) => {
        if (col.id === 'number') return idx + 1;
        return row[col.id || col.accessorKey] ?? '';
      })
    );
    const ws = XLSX.utils.aoa_to_sheet([headers, ...rows]);
    const wb = XLSX.utils.book_new();
    XLSX.utils.book_append_sheet(wb, ws, 'Sheet1');
    XLSX.writeFile(wb, 'table.xlsx');
  };
  const handleDownloadPDFButton = () => {
    const columns = getCurrentColumns();
    const columnOptions: ColumnOption[] = columns
      .filter((col) => col.header && col.header !== 'Actions' && col.header !== 'Delete')
      .map((col) => ({
        id: col.id || col.accessorKey,
        label: typeof col.header === 'function' ? col.header({}) : col.header,
      }));
    setDownloadColumns(columnOptions);
    setPendingDownloadData(paginatedData);
    setDownloadModalOpen(true);
  };

  function handleAddNew() {
    setEditRecord({});
    setViewRecord(null);
  }

  function renderSection() {
    // If viewing, render the view-only component for the section
    if (viewRecord) {
      switch (activeSection) {
        case 'beneficiaryMessages':
          return <BeneficiaryMessageView data={viewRecord} onClose={handleCloseView} />;
        case 'beneficiaries':
          return <BeneficiaryView data={viewRecord} onClose={handleCloseView} />;
        case 'beneficiaries':
          return <BeneficiaryView data={viewRecord} onClose={handleCloseView} />;
        case 'beneficiaryRequests':
        // Add similar cases for other sections, e.g. ProjectView, EventView, etc.
        // case 'projects':
        //   return <ProjectView data={viewRecord} onClose={handleCloseView} />;
        // --- Add other view components for other sections here ---
        default:
          return null;
      }
    }
    // If editing, render the edit form for the section
    if (editRecord) {
      switch (activeSection) {
        case 'projects':
          return (
            <CreateProjectForm
              initialValues={editRecord}
              onSuccess={handleSaveEdit}
              onCancel={handleCancelEdit}
            />
          );
        case 'reports':
          return (
            <CreateReportForm
              initialValues={editRecord}
              onSuccess={handleSaveEdit}
              onCancel={handleCancelEdit}
            />
          );
        case 'events':
          return (
            <CreateEventForm
              initialValues={editRecord}
              onSuccess={handleSaveEdit}
              onCancel={handleCancelEdit}
            />
          );
        case 'institutions':
          return (
            <CreateInstitutionForm
              initialValues={editRecord}
              onSuccess={handleSaveEdit}
              onCancel={handleCancelEdit}
            />
          );
        case 'beneficiaries':
          return (
            <CreateBeneficiaryForm
              initialValues={editRecord}
              onSuccess={handleSaveEdit}
              onCancel={handleCancelEdit}
            />
          );
        case 'beneficiaryMessages':
          return (
            <CreateBeneficiaryMessageForm
              initialData={editRecord}
              onSuccess={handleSaveEdit}
              onCancel={handleCancelEdit}
            />
          );
        case 'beneficiaryRequests':
          return (
            <CreateBeneficiaryRequestForm
              initialValues={editRecord}
              onSuccess={handleSaveEdit}
              onCancel={handleCancelEdit}
            />
          );
        case 'beneficiaryRequestResponse':
          return (
            <CreateBeneficiaryRequestResponseForm
              initialValues={editRecord}
              onSuccess={handleSaveEdit}
              onCancel={handleCancelEdit}
            />
          );
        case 'admin':
          return (
            <CreateAdminForm
              initialValues={editRecord}
              onSuccess={handleSaveEdit}
              onCancel={handleCancelEdit}
            />
          );
        case 'Home Page':
          return (
            <CreateHomePageForm
              initialValues={editRecord}
              onSuccess={handleSaveEdit}
              onCancel={handleCancelEdit}
            />
          );
        default:
          return null;
      }
    }

    // Pass all handlers to sections
    switch (activeSection) {
      case 'projects':
        return (
          <ProjectsSection
            paginatedData={paginatedData}
            page={page}
            rowsPerPage={rowsPerPage}
            handleEdit={handleEdit}
            handleView={handleView}
            handleDelete={handleDelete}
            TableActions={() => null}
            deleteId={deleteId}
            deleteLoading={deleteLoading}
          />
        );
      case 'reports':
        return (
          <ReportsSection
            paginatedData={paginatedData}
            page={page}
            rowsPerPage={rowsPerPage}
            handleEdit={handleEdit}
            handleView={handleView}
            handleDelete={handleDelete}
            TableActions={() => null}
            deleteId={deleteId}
            deleteLoading={deleteLoading}
          />
        );
      case 'events':
        return (
          <EventsSection
            paginatedData={paginatedData}
            page={page}
            rowsPerPage={rowsPerPage}
            handleEdit={handleEdit}
            handleView={handleView}
            handleDelete={handleDelete}
            TableActions={() => null}
            deleteId={deleteId}
            deleteLoading={deleteLoading}
          />
        );
      case 'institutions':
        return (
          <InstitutionsSection
            paginatedData={paginatedData}
            page={page}
            rowsPerPage={rowsPerPage}
            handleEdit={handleEdit}
            handleView={handleView}
            handleDelete={handleDelete}
            TableActions={() => null}
            deleteId={deleteId}
            deleteLoading={deleteLoading}
          />
        );
      case 'beneficiaries':
        return (
          <BeneficiariesSection
            paginatedData={paginatedData}
            page={page}
            rowsPerPage={rowsPerPage}
            handleEdit={handleEdit}
            handleView={handleView}
            handleDelete={handleDelete}
            messagesCountMap={{}}
            requestsCountMap={{}}
            currentUserRole=""
            TableActions={() => null}
            onAddMessage={() => {}}
            onAddRequest={() => {}}
            deleteId={deleteId}
            deleteLoading={deleteLoading}
          />
        );
      case 'beneficiaryMessages':
        return (
          <BeneficiaryMessagesSection
            paginatedData={paginatedData}
            page={page}
            rowsPerPage={rowsPerPage}
            handleEdit={handleEdit}
            handleView={handleView}
            handleDelete={handleDelete}
            currentUserRole=""
            TableActions={() => null}
            deleteId={deleteId}
            deleteLoading={deleteLoading}
          />
        );
      case 'beneficiaryRequests':
        return (
          <BeneficiaryRequestsSection
            paginatedData={paginatedData}
            page={page}
            rowsPerPage={rowsPerPage}
            handleEdit={handleEdit}
            handleView={handleView}
            handleDelete={handleDelete}
            responsesCountMap={{}}
            TableActions={() => null}
            onRespond={() => {}}
            deleteId={deleteId}
            deleteLoading={deleteLoading}
          />
        );
      case 'beneficiaryRequestResponse':
        return (
          <BeneficiaryRequestResponseSection
            paginatedData={paginatedData}
            page={page}
            rowsPerPage={rowsPerPage}
            handleEdit={handleEdit}
            handleView={handleView}
            handleDelete={handleDelete}
            TableActions={() => null}
            deleteId={deleteId}
            deleteLoading={deleteLoading}
          />
        );
      case 'charts':
        return <ChartsSection />;
      case 'admin':
        return (
          <AdminSection
            paginatedData={paginatedData}
            page={page}
            rowsPerPage={rowsPerPage}
            handleEdit={handleEdit}
            handleView={handleView}
            handleDelete={handleDelete}
            TableActions={() => null}
            deleteId={deleteId}
            deleteLoading={deleteLoading}
          />
        );
      case 'Home Page':
        return (
          <HomePageSection
            homepageData={data}
            messagesData={[]} // Not fetched
            handleEdit={handleEdit}
            handleView={handleView}
            handleDelete={handleDelete}
            showExecMsgAddButton={false}
            onAddExecMsg={() => {}}
            TableActions={() => null}
            homepageRef={undefined}
            messagesRef={undefined}
            deleteId={deleteId}
            deleteLoading={deleteLoading}
          />
        );
      default:
        return null;
    }
  }

  return (
    <div className="flex h-screen bg-gray-50 dark:bg-gray-900 transition-colors">
      {/* Sidebar */}
      <aside
        className={`transition-all duration-300 bg-white dark:bg-gray-950 border-r dark:border-gray-800 shadow-sm h-full ${
          sidebarCollapsed ? 'w-16' : 'w-60'
        } flex flex-col`}
      >
        <div className="flex items-center justify-between px-2 py-4 border-b dark:border-gray-800">
          <span className="text-xl font-bold text-pink-600 dark:text-pink-400 hidden sm:block">
            {!sidebarCollapsed && 'Dashboard'}
          </span>
          <button
            className="ml-auto p-2 text-gray-700 dark:text-gray-300 hover:text-pink-600 dark:hover:text-pink-400"
            onClick={() => setSidebarCollapsed((c) => !c)}
            aria-label={sidebarCollapsed ? 'Expand sidebar' : 'Collapse sidebar'}
          >
            {sidebarCollapsed ? <FaAngleRight size={18} /> : <FaAngleLeft size={18} />}
          </button>
        </div>
        <nav className="mt-2 flex-1 overflow-y-auto">
          {sections.map((section) => (
            <button
              key={section}
              onClick={() => handleMenuClick(section)}
              className={`w-full flex items-center gap-3 px-2 py-2 rounded transition-all font-medium
                ${
                  activeSection === section
                    ? 'bg-pink-100 dark:bg-pink-950 text-pink-600 dark:text-pink-400'
                    : 'text-gray-500 dark:text-gray-400 hover:bg-gray-100 dark:hover:bg-gray-900 hover:text-pink-700 dark:hover:text-pink-400'
                }
              `}
              style={{ minWidth: 0 }}
            >
              <span className="text-lg">{sectionIcons[section]}</span>
              {!sidebarCollapsed && <span className="truncate">{sectionLabels[section]}</span>}
            </button>
          ))}
        </nav>
      </aside>
      {/* Main Content */}
      <main className="flex-1 overflow-y-auto p-4 bg-white dark:bg-gray-900 transition-colors">
        {/* Top bar */}
        <div className="flex items-center gap-2 mb-8">
          <span className="text-2xl">{sectionIcons[activeSection]}</span>
          <span className="text-xl font-bold text-gray-700 dark:text-gray-100">
            {sectionLabels[activeSection]}
          </span>
        </div>

        {/* Table Controls */}
        <TableControls
          search={search}
          setSearch={setSearch}
          onAddNew={handleAddNew}
          exportExcel={handleExportExcel}
          downloadPDF={handleDownloadPDFButton}
          activeSection={activeSection}
          sectionLabels={sectionLabels}
        />

        {/* Section Content (table, forms, views) */}
        <div>{activeSection && <div className="mt-2">{renderSection()}</div>}</div>

        {/* Pagination under the table */}
        {pageCount > 1 && (
          <div className="flex gap-2 mt-4 justify-end items-center">
            <button
              className="px-2 py-1 rounded border dark:border-gray-700 dark:bg-gray-950 dark:text-gray-100"
              disabled={page === 1}
              onClick={() => setPage(page - 1)}
            >
              Prev
            </button>
            <span className="dark:text-gray-100">
              Page {page} of {pageCount}
            </span>
            <button
              className="px-2 py-1 rounded border dark:border-gray-700 dark:bg-gray-950 dark:text-gray-100"
              disabled={page === pageCount}
              onClick={() => setPage(page + 1)}
            >
              Next
            </button>
          </div>
        )}

        {/* Download Modal */}
        <DownloadColumnsModal
          isOpen={downloadModalOpen}
          onClose={() => setDownloadModalOpen(false)}
          columns={downloadColumns}
          onDownload={(selectedColumns) => {
            handleDownloadPDF(
              pendingDownloadData,
              selectedColumns,
              activeSection
                ? activeSection.charAt(0).toUpperCase() + activeSection.slice(1)
                : 'Data'
            );
          }}
        />
      </main>
    </div>
  );
}
