'use client';

import React from 'react';
import { DataTable } from '@/app/(admin)/admin/dashboard/data-table/data-table/data-table';
import { homepageColumns } from '@/app/(admin)/admin/dashboard/data-table/columns/homepage';
import { messageColumns } from '@/app/(admin)/admin/dashboard/data-table/columns/messages';

export default function HomePageSection({
  homepageData,
  messagesData,
  handleEdit,
  handleDelete,
  showExecMsgAddButton,
  onAddExecMsg,
  TableActions,
  homepageRef,
  messagesRef,
}: {
  homepageData: any[];
  messagesData: any[];
  handleEdit: (record: any) => void;
  handleDelete: (type: string, id: string | number) => void;
  showExecMsgAddButton: boolean;
  onAddExecMsg: () => void;
  TableActions: React.FC<any>;
  homepageRef: React.RefObject<HTMLDivElement>;
  messagesRef: React.RefObject<HTMLDivElement>;
}) {
  return (
    <div className="space-y-10">
      <div>
        <div className="flex justify-end mb-2">
          {showExecMsgAddButton && (
            <button
              className="inline-block rounded bg-blue-600 px-4 py-2 text-white hover:bg-blue-700"
              onClick={onAddExecMsg}
            >
              Add New Executive Message
            </button>
          )}
        </div>
        <div ref={messagesRef}>
          <DataTable
            columns={[
              {
                id: 'number',
                header: 'No.',
                cell: ({ row }: any) => row.index + 1,
                size: 50,
              },
              ...messageColumns({
                onEdit: handleEdit,
                onDelete: (id) => handleDelete('executive-messages', id),
              }),
            ]}
            data={messagesData || []}
          />
        </div>
        <div className="flex justify-end mt-2">
          <TableActions
            data={messagesData || []}
            columns={[
              {
                id: 'number',
                header: 'No.',
                cell: ({ row }: any) => row.index + 1,
                size: 50,
              },
              ...messageColumns({
                onEdit: handleEdit,
                onDelete: (id) => handleDelete('executive-messages', id),
              }),
            ]}
            tableRef={messagesRef}
          />
        </div>
      </div>
      <div ref={homepageRef}>
        <DataTable
          columns={[
            {
              id: 'number',
              header: 'No.',
              cell: ({ row }: any) => row.index + 1,
              size: 50,
            },
            ...homepageColumns({
              onEdit: handleEdit,
              onDelete: (id) => handleDelete('homepage-content', id),
            }),
          ]}
          data={homepageData || []}
        />
      </div>
      <div className="flex justify-end mt-2">
        <TableActions
          data={homepageData || []}
          columns={[
            {
              id: 'number',
              header: 'No.',
              cell: ({ row }: any) => row.index + 1,
              size: 50,
            },
            ...homepageColumns({
              onEdit: handleEdit,
              onDelete: (id) => handleDelete('homepage-content', id),
            }),
          ]}
          tableRef={homepageRef}
        />
      </div>
    </div>
  );
}
