'use client';

import React from 'react';
import { BeneficiaryMessage } from '@/lib/generated/prisma';

type BeneficiaryMessageWithRelations = BeneficiaryMessage & {
  beneficiary?: {
    id: string;
    firstName: string;
    lastName: string;
  };
};

export default function BeneficiaryMessageView({
  data,
  onClose,
}: {
  data: BeneficiaryMessageWithRelations;
  onClose?: () => void;
}) {
  return (
    <div className="max-w-xl mx-auto space-y-6 p-6 bg-background rounded-xl shadow">
      <h2 className="font-semibold text-xl mb-4">View Beneficiary Message</h2>
      <div className="space-y-2">
        <div>
          <span className="font-bold">Title:</span>
          <span className="ml-2">{data.title}</span>
        </div>
        <div>
          <span className="font-bold">Beneficiary:</span>
          <span className="ml-2">
            {data.beneficiary ? `${data.beneficiary.firstName} ${data.beneficiary.lastName}` : '-'}
          </span>
        </div>
        <div>
          <span className="font-bold">Status:</span>
          <span className="ml-2">{data.status}</span>
        </div>
        <div>
          <span className="font-bold">Created At:</span>
          <span className="ml-2">
            {data.createdAt ? new Date(data.createdAt).toLocaleDateString() : ''}
          </span>
        </div>
        <div>
          <span className="font-bold">Updated At:</span>
          <span className="ml-2">
            {data.updatedAt ? new Date(data.updatedAt).toLocaleDateString() : ''}
          </span>
        </div>
        <div>
          <span className="font-bold">Message:</span>
          <span className="ml-2 whitespace-pre-line">{data.beneficiaryMessageText as string}</span>
        </div>
      </div>
      {onClose && (
        <button
          type="button"
          className="mt-6 px-4 py-2 bg-pink-700 text-white rounded hover:bg-pink-900"
          onClick={onClose}
        >
          Close
        </button>
      )}
    </div>
  );
}
