'use client';

import { ColumnDef } from '@tanstack/react-table';
import { BeneficiaryRequest } from '@/lib/generated/prisma';
import { Button } from '@/components/ui/button';

type BeneficiaryRequestWithRelations = BeneficiaryRequest & {
  beneficiary?: {
    id: string;
    firstName: string;
    lastName: string;
  };
};

export function beneficiaryRequestColumns({
  onEdit,
  onView,
  onDelete,
}: {
  onEdit: (row: BeneficiaryRequestWithRelations) => void;
  onView?: (row: BeneficiaryRequestWithRelations) => void;
  onDelete: (id: string) => void;
}): ColumnDef<BeneficiaryRequestWithRelations>[] {
  return [
    {
      accessorKey: 'title',
      header: 'Title',
      cell: ({ row }) => row.getValue('title'),
    },
    {
      accessorKey: 'beneficiary',
      header: 'Beneficiary',
      cell: ({ row }) => {
        const beneficiary = row.original.beneficiary;
        return beneficiary ? `${beneficiary.firstName} ${beneficiary.lastName}` : '-';
      },
    },
    {
      accessorKey: 'status',
      header: 'Status',
      cell: ({ row }) => row.getValue('status') || '-',
    },
    {
      accessorKey: 'createdAt',
      header: 'Created At',
      cell: ({ row }) =>
        row.getValue('createdAt') ? new Date(row.getValue('createdAt')).toLocaleDateString() : '',
    },
    {
      accessorKey: 'updatedAt',
      header: 'Updated At',
      cell: ({ row }) =>
        row.getValue('updatedAt') ? new Date(row.getValue('updatedAt')).toLocaleDateString() : '',
    },
    {
      id: 'edit',
      header: 'Edit',
      cell: ({ row }) => (
        <Button
          type="button"
          size="sm"
          variant="outline"
          className="transition-colors duration-150 hover:bg-yellow-100 dark:hover:bg-yellow-800 hover:text-yellow-900 dark:hover:text-yellow-100"
          onClick={() => onEdit(row.original)}
        >
          Edit
        </Button>
      ),
    },
    {
      id: 'view',
      header: 'View',
      cell: ({ row }) =>
        onView ? (
          <Button
            type="button"
            size="sm"
            variant="outline"
            className="transition-colors duration-150 hover:bg-blue-100 dark:hover:bg-blue-800 hover:text-blue-900 dark:hover:text-blue-100"
            onClick={() => onView(row.original)}
          >
            View
          </Button>
        ) : null,
    },
    {
      id: 'delete',
      header: 'Delete',
      cell: ({ row }) => (
        <Button
          type="button"
          size="sm"
          variant="destructive"
          className="transition-colors duration-150 hover:bg-red-600 dark:hover:bg-red-800 hover:text-white dark:hover:text-white"
          onClick={() => onDelete(row.original.id)}
        >
          Delete
        </Button>
      ),
    },
  ];
}
