'use client';

import { useState } from 'react';
import { ColumnDef } from '@tanstack/react-table';
import { BeneficiaryMessage } from '@/lib/generated/prisma';
import { Button } from '@/components/ui/button';

/**
 * The Button component supports the "className" prop for custom styling.
 *
 * For theme-aware hover colors, use Tailwind's `hover:bg-...` and `dark:hover:bg-...`
 * utility classes on the button.
 */

type BeneficiaryMessageWithRelations = BeneficiaryMessage & {
  beneficiary?: {
    id: string;
    firstName: string;
    lastName: string;
  };
};

type Opts = {
  onEdit: (row: BeneficiaryMessageWithRelations) => void;
  onView: (row: BeneficiaryMessageWithRelations) => void;
  onDelete?: (id: string) => void;
  deleteId?: string | number | null;
  deleteLoading?: boolean;
  currentUserRole?: string;
};

export function beneficiaryMessageColumns({
  onEdit,
  onView,
  onDelete,
  currentUserRole,
  deleteId,
  deleteLoading,
}: Opts): ColumnDef<BeneficiaryMessageWithRelations>[] {
  const cols: ColumnDef<BeneficiaryMessageWithRelations>[] = [
    {
      accessorKey: 'title',
      header: 'Title',
      cell: ({ row }) => row.getValue('title'),
    },
    {
      accessorKey: 'beneficiary',
      header: 'Beneficiary',
      cell: ({ row }) => {
        const b = row.original.beneficiary;
        return b ? `${b.firstName} ${b.lastName}` : '-';
      },
    },
    {
      accessorKey: 'status',
      header: 'Status',
      cell: ({ row }) => (row.getValue('status') as string) || '-',
    },
    {
      accessorKey: 'createdAt',
      header: 'Created At',
      cell: ({ row }) =>
        row.getValue('createdAt')
          ? new Date(row.getValue('createdAt') as string).toLocaleDateString()
          : '',
    },
    {
      accessorKey: 'updatedAt',
      header: 'Updated At',
      cell: ({ row }) =>
        row.getValue('updatedAt')
          ? new Date(row.getValue('updatedAt') as string).toLocaleDateString()
          : '',
    },
    {
      id: 'actions',
      header: 'Actions',
      cell: ({ row }) => {
        const [deleteHovered, setDeleteHovered] = useState(false);
        const [viewHovered, setViewHovered] = useState(false);
        const [editHovered, setEditHovered] = useState(false);

        const isDeleting =
          onDelete &&
          currentUserRole !== 'beneficiary' &&
          deleteId === row.original.id &&
          deleteLoading;

        // Theme-aware hover classes
        const viewBtnClass =
          'transition-colors duration-150 hover:bg-blue-100 dark:hover:bg-blue-800 hover:text-blue-900 dark:hover:text-blue-100';
        const editBtnClass =
          'transition-colors duration-150 hover:bg-yellow-100 dark:hover:bg-yellow-800 hover:text-yellow-900 dark:hover:text-yellow-100';
        const deleteBtnClass =
          'transition-colors duration-150 hover:bg-red-600 dark:hover:bg-red-800 hover:text-white dark:hover:text-white';

        // For delete "in progress" warning style
        const deletingStyle = {
          cursor: 'not-allowed',
          backgroundColor: '#ffa726', // orange
          borderColor: '#ffa726',
          color: '#fff',
        };

        return (
          <div className="flex gap-2 items-center">
            <span
              onMouseEnter={() => setViewHovered(true)}
              onMouseLeave={() => setViewHovered(false)}
            >
              <Button
                type="button"
                size="sm"
                variant="outline"
                className={viewBtnClass}
                onClick={() => onView(row.original)}
                disabled={isDeleting}
              >
                View
              </Button>
            </span>
            <span
              onMouseEnter={() => setEditHovered(true)}
              onMouseLeave={() => setEditHovered(false)}
            >
              <Button
                type="button"
                size="sm"
                variant="outline"
                className={editBtnClass}
                onClick={() => onEdit(row.original)}
                disabled={isDeleting}
              >
                Edit
              </Button>
            </span>
            {onDelete && currentUserRole !== 'beneficiary' && (
              <span
                onMouseEnter={() => setDeleteHovered(true)}
                onMouseLeave={() => setDeleteHovered(false)}
                style={{ display: 'inline-block' }}
              >
                <Button
                  type="button"
                  size="sm"
                  variant={isDeleting ? 'warning' : 'destructive'}
                  className={deleteBtnClass}
                  style={isDeleting ? deletingStyle : {}}
                  onClick={() => !isDeleting && onDelete(row.original.id)}
                  disabled={isDeleting}
                >
                  {isDeleting ? (
                    <span>
                      <strong>This record will be deleted permanently</strong>
                    </span>
                  ) : (
                    'Delete'
                  )}
                </Button>
              </span>
            )}
          </div>
        );
      },
    },
  ];

  return cols;
}
