'use client';

import { useEffect, useMemo, useState } from 'react';
import { useRouter } from 'next/navigation';
import { Input } from '@/components/ui/input';
import { Button } from '@/components/ui/button';
import { Label } from '@/components/ui/label';

const publishOptions = ['draft', 'published'] as const;
type PublishStatus = (typeof publishOptions)[number];

interface BeneficiaryRequestInput {
  beneficiaryId: string; // parent FK (used only on CREATE)
  title: string;
  beneficiaryRequestText: string;
  status: PublishStatus;
}

interface InitialData {
  id: string; // <-- the row's PK (used for EDIT)
  beneficiaryId: string; // parent FK
  title: string;
  beneficiaryRequestText: string;
  status: PublishStatus;
}

// normalize to a safe controlled string
const s = (v: unknown) => (typeof v === 'string' ? v : '');
// normalize possibly stringified empties
const normalizeId = (v?: string) => (v && v !== 'undefined' && v !== 'null' ? v : '');

export default function CreateBeneficiaryRequestForm({
  beneficiaryId,
  editId,
  initialData,
  onSuccess,
  onCancel,
}: {
  beneficiaryId: string;
  editId?: string;
  initialData?: InitialData;
  onSuccess?: () => void;
  onCancel?: () => void;
}) {
  const router = useRouter();

  // Decide edit vs create robustly
  const effectiveEditId = useMemo(
    () => normalizeId(editId) || normalizeId(initialData?.id),
    [editId, initialData?.id]
  );
  const isEditing = Boolean(effectiveEditId);

  // Controlled defaults
  const [form, setForm] = useState<BeneficiaryRequestInput>({
    beneficiaryId: s(beneficiaryId),
    title: '',
    beneficiaryRequestText: '',
    status: 'draft',
  });

  const [loading, setLoading] = useState(false);
  const [loadingInitial, setLoadingInitial] = useState(isEditing && !initialData);

  // Load data for editing (or set create defaults)
  useEffect(() => {
    if (initialData) {
      setForm({
        beneficiaryId: s(initialData.beneficiaryId) || s(beneficiaryId),
        title: s(initialData.title),
        beneficiaryRequestText: s(initialData.beneficiaryRequestText),
        status: initialData.status ?? 'draft',
      });
      setLoadingInitial(false);
      return;
    }

    if (isEditing && effectiveEditId) {
      setLoadingInitial(true);
      fetch(`/api/beneficiaryRequests/${effectiveEditId}`)
        .then((res) => {
          if (!res.ok) throw new Error('Failed to fetch beneficiary request');
          return res.json();
        })
        .then((data) => {
          setForm({
            beneficiaryId: s(data.beneficiaryId) || s(beneficiaryId),
            title: s(data.title),
            beneficiaryRequestText: s(data.beneficiaryRequestText),
            status: (publishOptions as readonly string[]).includes(data.status)
              ? (data.status as PublishStatus)
              : 'draft',
          });
        })
        .catch(() => {
          alert('Error loading existing request data.');
        })
        .finally(() => setLoadingInitial(false));
      return;
    }

    // Create mode defaults
    setForm({
      beneficiaryId: s(beneficiaryId),
      title: '',
      beneficiaryRequestText: '',
      status: 'draft',
    });
  }, [initialData, isEditing, effectiveEditId, beneficiaryId]);

  const handleChange = (e: React.ChangeEvent<HTMLInputElement | HTMLSelectElement>) => {
    const { name, value } = e.target;
    setForm((prev) => ({
      ...prev,
      [name]: name === 'status' ? (value as PublishStatus) || 'draft' : s(value),
    }));
  };

  const handleRequestTextChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setForm((prev) => ({ ...prev, beneficiaryRequestText: s(e.target.value) }));
  };

  const handleSubmit = async (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    setLoading(true);

    try {
      // On EDIT, do not send beneficiaryId (keep parent FK fixed)
      const payload = isEditing
        ? {
            title: form.title,
            beneficiaryRequestText: form.beneficiaryRequestText,
            status: form.status,
          }
        : {
            beneficiaryId: form.beneficiaryId,
            title: form.title,
            beneficiaryRequestText: form.beneficiaryRequestText,
            status: form.status,
          };

      const url = isEditing
        ? `/api/beneficiaryRequests/${effectiveEditId}`
        : '/api/beneficiaryRequests';

      const method = isEditing ? 'PATCH' : 'POST';

      const res = await fetch(url, {
        method,
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(payload),
      });

      if (!res.ok) {
        const error = await res.json().catch(() => ({}));
        throw new Error(error.error || 'Failed to save beneficiary request');
      }

      onSuccess ? onSuccess() : router.refresh();
    } catch {
      alert('There was an error saving the beneficiary request.');
    } finally {
      setLoading(false);
    }
  };

  if (loadingInitial) {
    return (
      <div className="flex items-center justify-center min-h-40">
        <span className="text-gray-600">Loading...</span>
      </div>
    );
  }

  return (
    <form
      onSubmit={handleSubmit}
      className="max-w-xl mx-auto space-y-6 p-6 bg-background rounded-xl shadow"
      autoComplete="off"
    >
      <h2 className="font-semibold text-xl mb-4">
        {isEditing ? 'Edit Beneficiary Request' : 'Create Beneficiary Request'}
      </h2>

      {/* Hidden parent id: used only on CREATE, but keeping it controlled is harmless */}
      <Input type="hidden" name="beneficiaryId" value={form.beneficiaryId} />

      <div className="space-y-2">
        <Label htmlFor="title">Title</Label>
        <Input id="title" name="title" value={form.title} onChange={handleChange} required />
      </div>

      <div className="space-y-2">
        <Label htmlFor="beneficiaryRequestText">Request Text (JSON or text)</Label>
        <Input
          id="beneficiaryRequestText"
          name="beneficiaryRequestText"
          value={form.beneficiaryRequestText}
          onChange={handleRequestTextChange}
          required
        />
      </div>

      <div className="space-y-2">
        <Label htmlFor="status">Publish Status</Label>
        <select
          id="status"
          name="status"
          value={form.status ?? 'draft'}
          onChange={handleChange}
          className="w-full border border-input rounded-md p-2 text-sm bg-background text-foreground"
        >
          {publishOptions.map((status) => (
            <option key={status} value={status}>
              {status}
            </option>
          ))}
        </select>
      </div>

      <div className="flex gap-3">
        <Button
          type="submit"
          disabled={loading || (isEditing && !effectiveEditId)}
          className="flex-1 bg-[#9f004d] hover:bg-[#7c003c] text-white"
        >
          {loading
            ? isEditing
              ? 'Saving...'
              : 'Creating...'
            : isEditing
            ? 'Save Changes'
            : 'Create Request'}
        </Button>
        {onCancel && (
          <Button
            type="button"
            variant="outline"
            className="flex-1 border-[#9f004d] text-[#9f004d] hover:bg-[#f5e3ec]"
            onClick={onCancel}
            disabled={loading}
          >
            Cancel
          </Button>
        )}
      </div>
    </form>
  );
}
