'use client';

import React from 'react';

export default function BeneficiaryView({ data, onClose }: { data: any; onClose?: () => void }) {
  return (
    <div className="max-w-xl mx-auto space-y-4 p-6 bg-background rounded-xl shadow">
      <h2 className="font-semibold text-xl mb-4">Beneficiary</h2>
      <div>
        <strong>Name:</strong> {data.firstName} {data.lastName}
      </div>
      <div>
        <strong>Gender:</strong> {data.gender}
      </div>
      <div>
        <strong>Email:</strong> {data.email}
      </div>
      <div>
        <strong>Phone:</strong> {data.phone}
      </div>
      <div>
        <strong>Date of Birth:</strong>{' '}
        {data.dateOfBirth ? new Date(data.dateOfBirth).toLocaleDateString() : ''}
      </div>
      <div>
        <strong>Status:</strong> {data.beneficiaryStatus}
      </div>
      <div>
        <strong>Images:</strong> {data.images?.join(', ')}
      </div>
      <div>
        <strong>Institution:</strong> {data.institution?.name}
      </div>
      <div>
        <strong>Created At:</strong> {new Date(data.createdAt).toLocaleString()}
      </div>
      <div>
        <strong>Updated At:</strong> {new Date(data.updatedAt).toLocaleString()}
      </div>
      {onClose && (
        <button className="mt-4 px-4 py-2 bg-pink-700 text-white rounded" onClick={onClose}>
          Close
        </button>
      )}
    </div>
  );
}
